html, body, #root, .App, .container {
  height: 100%;
}

.App {
  text-align: center;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  flex-flow: row;
  gap: 69px;
  align-items: center;
  justify-content: center;
  justify-items: center;
  align-content: center
}

.left {
  z-index: 1;
  backdrop-filter: blur(5px);
  background: linear-gradient(#e7e7e76b, #ffffff18);
  border-radius: 5px;
  box-shadow: 5px 6px 35px #2b2b2b;
}

.right {
  z-index: 1;
  backdrop-filter: blur(5px);
  background: linear-gradient(#e7e7e76b, #ffffff18);
  border-radius: 5px;
  box-shadow: 5px 6px 35px #2b2b2b;
}
.center {
  z-index: 1;
  backdrop-filter: blur(5px);
  background: linear-gradient(#e7e7e76b, #ffffff18);
  border-radius: 5px;
  box-shadow: 5px 6px 35px #2b2b2b;
}

@media screen and (max-width: 1100px) {
  .container {
    grid-template-columns: 1fr 1fr;    
  }
}

@media screen and (max-width: 650px) {
  .container {
    grid-template-columns: 1fr;
  }
}